
.home-wrapper {
  width: 100%;
  background: #f7f7f7;
}
.home-wrapper .swiper-container {
  height: 240px;
}
.home-wrapper .swiper-container .swiper-wrapper img {
  width: 100%;
  -webkit-filter: brightness(0.8) grayscale(0.4) sepia(0.2);
          filter: brightness(0.8) grayscale(0.4) sepia(0.2);
}
.home-wrapper .swiper-container .swiper-wrapper p {
  position: absolute;
  top: 130px;
  right: 20px;
  width: 80%;
  height: 50px;
  text-align: right;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  font-size: 20px;
  font-weight: bold;
  line-height: 25px;
  color: #fff;
  text-shadow: 1px 1px 10px rgba(0,0,0,0.5);
}
.home-wrapper .swiper-container .swiper-wrapper p:after {
  display: block;
  position: absolute;
  content: "";
  right: 0px;
  bottom: -10px;
  width: 120px;
  border: 2px solid #2196f3;
}
.home-wrapper .swiper-container .swiper-pagination {
  left: 30%;
}
.home-wrapper .home-list-container {
  margin-top: 10px;
  overflow: hidden;
}
.home-wrapper .home-list-container .list-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 15px;
  height: 100px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #fff;
  box-shadow: 0 6px 6px -3px rgba(0,0,0,0.2), 0 10px 14px 1px rgba(0,0,0,0.14), 0 4px 18px 3px rgba(0,0,0,0.12);
  border-radius: 5px;
}
.home-wrapper .home-list-container .list-item img {
  margin-left: 10px;
  width: 80px;
  height: 80px;
}
.home-wrapper .home-list-container .list-item p {
  padding-left: 20px;
  padding-right: 20px;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}


